// Google Fonts
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
  font-family: "Poppins", sans-serif;
}
html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden;
  overflow-y: overlay;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: black !important;
  font-family: "Poppins", sans-serif;
}
body {
  display: block;
}
h1,
h2,
h3,
h4,
h5,
li,
ul,
ol {
  margin: 0;
  padding: 0;
}
a {
  color: inherit;
  text-decoration: none;
}
ul,
li {
  list-style: none;
}
.header-container {
  height: 100vh;
  width: 100%;
  background: url("https://images.unsplash.com/photo-1645088520336-62d94324e869?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2070&q=80");
  background-size: cover;
  background-position: center;

  display: flex;
  flex-direction: column;
}
.main-nav {
  height: 10vh;
  width: 100%;
  // background-color: rgba(255, 0, 0, 0.281);
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  box-sizing: border-box;
  padding: 0 2rem;
}
.heading {
  font-size: 30pt;
  font-weight: 800;
}
.subheading {
  font-size: 18pt;
  font-weight: 500;
}

/* -------- NavBar --------- */

.nav-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 80%;
  min-width: 50%;
  margin-right: 40px;
  li {
    flex-wrap: nowrap;
    color: white;
    font-weight: 600;
  }
}

nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

nav Link {
  margin: 0 2rem;
}

/* Underline styles */
nav li {
  display: block;
  position: relative;
  padding: 0.2em 0;
}

/* Fade in */
nav li::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0.1em;
  background-color: rgb(255, 255, 255);
  opacity: 0;
  transition: opacity 300ms, transform 300ms;
}

nav li:hover::after,
nav li:focus::after {
  opacity: 1;
  transform: translate3d(0, 0.2em, 0);
}

nav li {
  overflow: hidden;
}

nav li::after {
  opacity: 1;
  transform: translate3d(-101%, 0, 0);
}

nav li:hover::after,
nav li:focus::after {
  transform: translate3d(0, 0, 0);
}
/*-----------------------*/
/* MOBILE NAVIGATION */
.menunavigation {
  position: absolute;
}
.menunavigation {
  margin: 0;
  padding: 0;
  background: hsl(0, 100%, 26%);
  color: hsl(0, 100%, 26%);
  /*font-family: "Avenir Next", "Avenir", sans-serif;*/
}
#menuToggle {
  display: block;
  position: relative;
  top: 50px;
  left: 30px;

  z-index: 9999999999;

  -webkit-user-select: none;
  user-select: none;
}
#menuToggle Link {
  text-decoration: none;
  color: black;
  transition: color 0.3s ease;
}
#menuToggle Link:hover {
  color: hsl(191, 100%, 26%);
}
#menuToggle input {
  display: block;
  width: 40px;
  height: 32px;
  position: absolute;
  top: -7px;
  left: -5px;

  cursor: pointer;

  opacity: 0;
  z-index: 9999999999;

  -webkit-touch-callout: none;
}
#menuToggle span {
  display: block;
  width: 33px;
  height: 4px;
  margin-bottom: 5px;
  position: relative;

  background: white;
  border-radius: 3px;

  z-index: 9999999999;

  transform-origin: 4px 0px;

  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
    background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
}
#menuToggle span:first-child {
  transform-origin: 0% 0%;
}
#menuToggle span:nth-last-child(2) {
  transform-origin: 0% 100%;
}
#menuToggle input:checked ~ span {
  opacity: 1;
  transform: rotate(45deg) translate(-2px, -1px);
  background: black;
}
#menuToggle input:checked ~ span:nth-last-child(3) {
  opacity: 0;
  transform: rotate(0deg) scale(0.2, 0.2);
}
#menuToggle input:checked ~ span:nth-last-child(2) {
  transform: rotate(-45deg) translate(0, -1px);
}
#menu {
  position: absolute;
  width: 300px;
  height: 200vh;
  margin: -100px 0 0 -50px;
  padding: 50px;
  padding-top: 125px;

  background: white;
  list-style-type: none;
  -webkit-font-smoothing: antialiased;
  /* to stop flickering of text in safari */

  transform-origin: 0px 0px;
  transform: translate(-100%, 0);

  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  display: block;
  z-index: 99999999999;
}
#menu li {
  padding: 25px 0;
  font-size: 22px;
  font-weight: 700;
}
#menuToggle input:checked ~ ul {
  transform: none;
}

.mobilelogo {
  position: relative;
  /* float: right; */
  padding-top: 15px;
  padding-right: 50px;
  color: white;
  top: 40px;
  right: 10px;
}
.mobilenavigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
