/*-------------- Nav Responsiveness -------------*/

@media (min-width: 993px) {
  .mobilenavigation {
    display: none;
  }
}
@media (max-width: 992px) {
  .main-nav {
    display: none;
  }
}

/* -------------- Header Styling -------------- */

header {
  height: 100%;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 3rem;
  padding-bottom: 6rem;
  position: relative;
}
.header-title {
  font-size: 50pt;
  font-weight: 800;
  height: 5rem;
}

.job-title {
  font-size: 15pt;
  margin: 0;
  letter-spacing: 5px;
}

.blurb {
  width: 80%;
  font-size: 18pt;
}

@media (max-width: 768px) {
  header {
    justify-content: center;
    padding: 2rem;
  }

  .header-title {
    height: auto;
    line-height: 90%;
    font-size: 25pt;
  }
}
/* -------------- Summary -------------- */

.summary {
  background-color: rgb(255, 255, 255);
  height: auto;
  padding: 3rem 0;
  .heading {
    margin-bottom: 3rem;
  }
  filter: invert(1);
}
